import React from 'react'

const BioSection = () => {
    return (
        <section className='bio-section' id="bio">
            <h2>Bio</h2>
            <p>Formada na UFPE/CAC Centro de Artes e Comunicao da Universidade Federal de Pernambuco em
                1994, com ps graduaes e especializaes em Gesto Ambiental pela Uneal  Universidade
                Estadual de Alagoas e Especializao em Gesto Ambiental e Urbanizao de Assentamentos
                Precrios pelo Ministrio das Cidades (2008). Exerceu a funo de professora substituta na
                UFAL  Universidade Federal de Alagoas- Campus Arapiraca e atualmente cursa Mestrado em
                Dinmicas Territoriais e Cultura pela UNEAL Universidade Estadual de Alagoas. Com atuao em
                projetos arquitetnicos e urbansticos, residenciais e comerciais.
            </p>
            <p>
                Profissional proponente do projeto Bosques Urbanos das Arapiracas para o Concursos UIA Unio
                Internacional dos Arquitetos em 2002, que resultou no projeto Bosques das Arapiracas,
                impactando na rea central da cidade de Arapiraca em Alagoas. Responsvel pelo Projeto
                Integrado Jardim das Paineiras, que beneficiou 583 famlias atravs do programa HBB 
                Habitar Brasil Bid, remanejando famlias para novos conjuntos habitacionais e criando um
                grande corredor verde que se tornou carto postal no centro da cidade de Arapiraca, alm de
                uma grande rea de pulmo verde onde drenagem e proteo ambiental so condicionantes
                principais.
            </p>
            <p>Desde 2001, atua em projetos sociais e culturais na regio do agreste alagoano atravs da ONG
                Candeeiro Aceso, a qual  integrante fundadora e faz parte do corpo tcnico de elaborao de
                projetos nas duas reas.
            </p>
            <p>
                Foi secretria de Cultura, Lazer e Juventude da Prefeitura de Arapiraca, onde atuou para
                viabilizar a Lei Emergencial Aldir Blanc em sua primeira edio atravs de editais e
                procedimentos para a comunidade cultural acessar recursos provenientes do Governo Federal.
            </p>
            <p>
                Em seu escritrio baseado em Alagoas, atua em consultoria de planos diretores e planos
                habitacionais, projetos urbansticos de loteamentos e condomnios, bem como em projetos
                residenciais e comerciais de alto padro. Integra a equipe idealizadora da Startup Phi
                Criativo, premiada pelo Ministrio da Cincia e Tecnologia como projeto inovador.
            </p>
            <p>Atualmente  Conselheira Federal Suplente do CAU-BR - Conselho de Arquitetos e Urbanistas
                gesto 2023/2026, e vice-presidente do Instituto de Arquitetos do Brasil - Departamento
                Alagoas. Foi conselheira regional do CAU de Alagoas de 2012 a 2023, e conselheira regional
                suplente entre2018a2020.</p>
        </section>
    )
}

export default BioSection
