import React from 'react'
import PortfolioImage from './PortfolioImage'

const Portfolio = () => {
    return (
        <section id="portfolio" className="portfolio">
            <span className='span-show'>Portflio</span>
            <h2>Conhea nossos projetos</h2>
            <div className='portfolio-container'>
                <PortfolioImage />
                <div className='portfolio-text'>
                    <a href="">
                        <span>Arquitetura, Urbanismo e Paisagismo</span>
                        <h2>Portflio</h2>
                        <p>Conhea alguns de nossos projetos de Arquitetura Residncial, Urbanismo,
                            Paisagismo, Ambientes e Mostras de Arquitetura.</p>
                        <span className='portfolio-anchor'>Veja nosso portflio</span>
                    </a>
                </div>
            </div>
        </section>
    )
}

export default Portfolio
