import React from 'react';
import CountUp from 'react-countup';
import Cards from './Cards';

const CardsSection = () => {

    const duration = 2;

    const cardsData = [
        {
            title: 'Projetos Arquitetnicos',
            description:
                'Da concepo  construo, nossa equipe cria espaos que combinam funcionalidade e beleza. De residncias a edifcios comerciais, tornamos sua viso uma inspiradora realidade arquitetnica.',
        },
        {
            title: 'Projetos Urbansticos',
            description:
                'Compreendemos o pulso das cidades modernas. Nossos projetos urbansticos no apenas transformam reas, mas tambm criam comunidades vibrantes e sustentveis. Junte-se a ns para moldar o futuro das nossas cidades.',
        },
        {
            title: 'Interiores',
            description:
                'Seja em casa ou no escritrio, o design de interiores  crucial para criar ambientes acolhedores e funcionais. Deixe-nos guiar voc por um processo que transforma cada espao em uma expresso autntica do seu estilo de vida.',
        },
        {
            title: 'Consultorias e mais',
            description:
                'Para tomar decises informadas em seus projetos, confie na nossa consultoria em arquitetura. Desde orientaes de design at anlises de viabilidade, estamos aqui para tornar sua jornada arquitetnica mais suave e eficiente.',
        }
    ]

    return (
        <section className='cards-section'>
            <div className='cards-content'>
                <div className='cards-content-item'><h2><strong className='cards-content-item-number'><CountUp start={0} end={30} duration={duration} /> </strong><span>Anos de Experincia</span></h2></div>

                <div className='cards-content-item cards-content-item-border'><h2><strong className='cards-content-item-number'><CountUp start={0} end={600} duration={duration} />+</strong><span> Projetos Concludo</span></h2></div>

                <div className='cards-content-item'><h2><strong className='cards-content-item-number'><CountUp start={0} end={30} duration={duration} />+</strong><span> Parceiros e Fornecedores</span></h2></div>
            </div>

            <h2 className='cards-item-h2'>Atuao</h2>

            <div className='cards-item'>
                {cardsData.map((card, index) => (
                    <Cards
                        key={index}
                        title={card.title}
                        description={card.description}
                        cardNumber={index + 1}
                    />
                ))}
            </div>
        </section>
    )
}

export default CardsSection
